@font-face {
  font-family: "Oswald";
  src: url("..//fonts//oswaldmedium.woff2"), url("..//fonts/oswaldmedium.woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("..//fonts/oswaldregular.woff2"), url("..//fonts/oswaldregular.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  background-color: #ffffff;
}
* {
  box-sizing: inherit;
}
a {
  text-decoration: none;
  color: #000000;
}
img {
  width: 100%;
  height: auto;
}
picture {
  width: 0;
  height: 0;
}
.title {
  font-size: 36px;
  line-height: 1;
  color: #111111;
  font-weight: 400;
  margin-right: 0;
}
.title--green-line {
  display: flex;
  width: 100%;
}
.title--green-line::after {
  content: "";
  margin-left: 6.5%;
  height: 0.59em;
  flex-grow: 1;
  border-bottom: 3px solid #68b738;
}
@media (min-width: 768px) {
  .title--green-line::before {
    content: "";
    margin-right: 5%;
    height: 0.59em;
    width: 4%;
    border-bottom: 3px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .title {
    font-size: 60px;
    line-height: 1;
  }
  .title--green-line::before {
    margin-right: 2.6%;
    width: 5%;
  }
  .title--green-line::after {
    margin-left: 3%;
  }
}
.button {
  display: block;
  min-width: 245px;
  min-height: 33px;
  margin: 0 auto;
  padding-top: 10px;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.main-nav {
  border-bottom: 1px solid #eaeaea;
}
.main-nav__logo-wrapper {
  display: inline-block;
  position: relative;
  width: 191px;
}
.main-nav__logo-wrapper:hover {
  opacity: 0.8;
}
.main-nav__logo-wrapper:active {
  opacity: 0.6;
}
.main-nav__container {
  width: 280px;
  margin: 0 auto;
  padding-top: 12px;
  padding-bottom: 12px;
}
.main-nav__toggle {
  border: none;
  background: none;
  position: absolute;
  top: 24px;
  right: 16px;
  width: 28px;
  height: 18px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-mobile-menu.svg");
  outline: none;
}
.main-nav__toggle--close {
  background-image: url("../img/icon-close.svg");
  height: 18px;
  width: 25px;
}
@keyframes menu__falling {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
.main-nav__list--open {
  display: block;
  animation: menu__falling 0.6s;
}
.main-nav__item {
  min-height: 67px;
  text-align: center;
  border-top: 1px solid transparent;
  border-bottom: 1px solid #eaeaea;
}
.main-nav__item:first-of-type {
  border-top: 1px solid #eaeaea;
}
.main-nav__link {
  display: block;
  margin-top: 18px;
  text-transform: uppercase;
  font-size: 20px;
  color: #111111;
  line-height: 1;
  font-weight: 500;
}
.main-nav__link:hover {
  color: rgba(17, 17, 17, 0.6);
}
.main-nav__link:active {
  color: rgba(17, 17, 17, 0.3);
}
.main-nav__link:focus {
  outline: none;
  color: #b738a7;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav {
    display: flex;
    justify-content: flex-start;
    margin-left: 4%;
    margin-right: 4%;
    align-items: flex-end;
    border: none;
  }
  .main-nav__logo-wrapper {
    width: 172px;
    height: 60px;
    margin-left: -18px;
  }
  .main-nav__container {
    width: auto;
    margin-top: 24px;
    padding: 0;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    width: 73%;
  }
  .main-nav__list--close {
    animation: none;
  }
  .main-nav__item {
    width: auto;
    margin-left: 6.8%;
    border: none;
  }
  .main-nav__item:first-of-type {
    border: none;
  }
  .main-nav__link {
    margin-top: 23px;
    font-size: 18px;
    line-height: 1.39;
    color: #222222;
    font-weight: normal;
  }
  .main-nav__link--active::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: #68b738;
    margin-top: 6px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    justify-content: space-between;
    margin-left: 7.6%;
    margin-right: 7.6%;
    padding-top: 32px;
    z-index: 2;
  }
  .main-nav__logo-wrapper {
    margin: 0;
    width: 202px;
  }
  .main-nav__container {
    margin: 0;
    margin-top: 21px;
  }
  .main-nav__list {
    justify-content: flex-end;
    margin: 0;
  }
  .main-nav__item {
    margin-left: 35px;
    border: none;
  }
  .main-nav__link {
    color: #222222;
    font-size: 20px;
    line-height: 1.25;
    font-weight: 400;
  }
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--index:focus:first-of-type::after {
    background-color: #b738a7;
  }
  .main-nav__link--index:first-of-type::after {
    background-color: #ffffff;
  }
  .main-nav__link--index:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav__link--index:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
