.programs {
  min-width: 320px;
  margin-bottom: 29px;
}
.programs__list {
  margin: 0;
  min-width: 280px;
  padding-right: 20px;
  padding-left: 20px;
  list-style: none;
}
.programs__item {
  position: relative;
  margin-bottom: 20px;
  padding: 24px 20px 19px 19px;
  border-top: 1px solid transparent;
  background-color: #f2f2f2;
}
.programs__icon {
  position: relative;
  float: left;
  margin-left: 3px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #68b738;
}
.programs__icon--slim::before {
  content: "";
  position: absolute;
  left: calc(50% - 70% / 2);
  top: -6%;
  display: block;
  width: 70%;
  height: 98%;
  background-image: url("..//img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.programs__icon--gaining::before {
  content: "";
  position: absolute;
  top: calc(50% - 56% / 2);
  left: calc(50% - 134% / 2);
  display: block;
  width: 134%;
  height: 56%;
  background-image: url("..//img/icon-cat-pro.svg");
  background-repeat: no-repeat;
}
.programs__title {
  margin: 0;
  margin-top: 5px;
  margin-left: 72px;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
