  padding-top: 29px;
  margin-left: 1px;
  margin-bottom: 40px;
  line-height: 18px;
  font-family: "Arial", "Helvetica", sans-serif;
  text-align: left;
  color: #444444;
}
.programs__description::after {
  content: "";
  position: absolute;
  bottom: -23px;
  left: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #000000;
  opacity: 0.1;
}
.programs__link {
  position: relative;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #111111;
}
.programs__link:hover::after {
  background-image: url("..//img/icon-right-arrow-verylong.svg");
  background-position: 0 0;
  width: 35px;
}
.programs__link:active {
  color: rgba(17, 17, 17, 0.3);
}
.programs__link:active::after {
  opacity: 0.3;
}
.programs__link:focus {
  outline: none;
  color: #68b738;
}
.programs__link::after {
  content: "";
  position: absolute;
  display: block;
  width: 25px;
  height: 12px;
  top: 8px;
  left: 98px;
  background-image: url("..//img/icon-right-arrow-long.svg");
